import React, { useState } from "react";
import "../../../shared/report.css";
import "../../../shared/button.css";
import "../../../shared/settings.css";
import { Table } from "mtforms";
import { columns } from "./column";
import {
  useAddAdmin,
  useDeleteUser,
  useGetLocation,
  useGetUsers,
  useUpdateUser,
} from "../hooks";
import Modal from "../../../components/Modal";
import { Button, FormGroup, Input, Select } from "mtforms";
import { useIsMutating } from "@tanstack/react-query";
import { toast } from "react-toastify";
import { errorAlert, successAlert, toastOptions } from "../../../utils";
import swal from "sweetalert";
import { PrivatePaths } from "../../../routes/path";
import { useNavigate } from "react-router-dom";
import Layout from "../../../layout";
import { ThreeDots } from "react-loader-spinner";
import excel from "../../../assets/excel.png";
import * as XLSX from "xlsx";
import excelTemp from "../../../assets/templates/StaffTemplate.xlsx";
import { FaFileExcel } from "react-icons/fa";
import { useBulkRegister } from "../../Auth/Register/hooks";

const Users = () => {
  const { data } = useGetUsers();
  const [user, setUser] = useState(true);
  const [open, setOpen] = useState(false);
  const [formData, setFormData] = useState({});
  const [openEdit, setOpenEdit] = useState(false);
  const [singleStaff, setSingleStaff] = useState(false);
  const [errors, setErrors] = useState({});
  const { mutate, isSuccess, reset } = useAddAdmin();
  const navigate = useNavigate();
  const { data: locations } = useGetLocation();
  // console.log("user:",data)
  const [openBulk, setOpenBulk] = useState(false);
  const [upload, setUpload] = useState(false);
  const [excelData, setExcelData] = useState([]);
  // get admins from data
  const admin = data?.filter((x) => x.is_admin === true);

  const isLoading = useIsMutating();

  const handleChange = (name, value) => {
    setFormData({ ...formData, [name]: value });
  };
  const validationHandler = (name, error) => {
    setErrors({ ...errors, [name]: error });
  };

  const {
    mutate: remove,
    isError: isEDelete,
    isSuccess: isSDelete,
    reset: eReset,
    error: Error,
  } = useDeleteUser();

  if (isEDelete) {
    eReset();

    errorAlert(Error);
  }
  if (isSDelete) {
    eReset();
    toast.success(`User Deleted Successfully`, toastOptions);
  }

  const {
    mutate: removeAdmin,
    isError: isRemoveAdminErr,
    isSuccess: isRemoveAdminSuccess,
    reset: eRemoveAdminReset,
    error: ErrorRA,
  } = useUpdateUser();

  const {
    mutate: updateUser,
    isError: isUpdateErr,
    isSuccess: isUpdateSuccess,
    reset: eUpdateReset,
    error: updateError,
  } = useUpdateUser();

  if (isRemoveAdminErr) {
    eRemoveAdminReset();

    errorAlert(ErrorRA);
  }
  if (isRemoveAdminSuccess) {
    eRemoveAdminReset();
    toast.success(`Successful`, toastOptions);
  }

  if (isUpdateErr) {
    eUpdateReset();

    errorAlert(updateError);
  }
  if (isUpdateSuccess) {
    eUpdateReset();
    toast.success(`user updated successfully`, toastOptions);
    setFormData({});
    setOpenEdit(false);
  }

  const submitHandler = () => {
    mutate(formData);
  };

  if (isSuccess) {
    setFormData({});
    setOpen(false);
    reset();
    toast.success(`Admin added Successfully`, toastOptions);
  }

  const editHandler = () => {
    updateUser(formData);
  };

  const closeHandler = () => {
    setOpenEdit(false);
    setFormData({});
  };

  const actions = (item) => [
    {
      name: "Attendance History",
      onClick: (res) => {
        navigate(`${PrivatePaths.ADMIN_USER_HISTORY}/${res.id}`);
      },
    },
    {
      name: "Edit User",
      onClick: (res) => {
        setFormData(res);
        setOpenEdit(true);
      },
    },
    {
      name: "Delete User",
      onClick: (res) => {
        swal({
          title: "Are you sure?",
          text: "Once deleted, you will not be able to recover this",
          icon: "warning",
          //    @ts-ignore
          buttons: true,
          dangerMode: true,
        }).then((willDelete) => {
          if (willDelete) {
            remove(res.id);
          }
        });
      },
    },
    {
      name: "Add as Admin",
      onClick: (res) => {
        swal({
          title: "Are you sure?",
          text: "This user will have admin privilegde",
          icon: "warning",
          //    @ts-ignore
          buttons: true,
          dangerMode: true,
        }).then((willDelete) => {
          if (willDelete) {
            const addAdminData = { ...formData, id: res.id, is_admin: true };
            removeAdmin(addAdminData);
          }
        });
      },
    },
  ];

  const adminActions = (item) => [
    {
      name: "Remove as Admin",
      onClick: (res) => {
        swal({
          title: "Are you sure?",
          text: "This user will no longer have admin privilegde",
          icon: "warning",
          //    @ts-ignore
          buttons: true,
          dangerMode: true,
        }).then((willDelete) => {
          if (willDelete) {
            const addAdminData = { ...formData, id: res.id, is_admin: false };
            removeAdmin(addAdminData);
          }
        });
      },
    },
  ];

  const singleStaffHandler = () => {
    setSingleStaff(!singleStaff);
    setFormData("");
  };

  const openBulkHandler = () => {
    setOpenBulk(!openBulk);
    setFormData("");
  };

  const fileChange = (e) => {
    setUpload(true);
    const file = e.target.files[0];
    var allowedExtensions =
      "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" ||
      "application/vnd.ms-excel" ||
      ".csv";
    if (file.type !== allowedExtensions) {
      setUpload(false);
      return toast.error("Invalid File", toastOptions);
    } else {
      var reader = new FileReader();
      reader.onload = function (e) {
        var data = reader.result;
        let readedData = XLSX.read(data, { type: "binary" });
        const wsname = readedData.SheetNames[0];
        const ws = readedData.Sheets[wsname];
        /* Convert array to json*/
        const dataParse = XLSX.utils.sheet_to_json(ws);
        if (dataParse.length === 0) {
          setUpload(false);
          toast.error("Document is empty", toastOptions);

          return;
        } else {
          setUpload(false);
          setExcelData(dataParse);
          // toast.success("Upload Successful");
        }
      };

      reader.readAsBinaryString(file);
      // history.push("/newclaim/screen/2")
    }
  };

  const {
    mutate: bulk,
    isSuccess: isBulkSuccess,
    isError: isBulkError,
    error: bulkError,
    reset: bulkReset,
  } = useBulkRegister();

  if (isBulkSuccess) {
    bulkReset();
    successAlert("Uploaded Successfully");
    setOpenBulk(false);
    setExcelData([]);
    setSingleStaff(false);
  }
  if (isBulkError) {
    bulkReset();
    errorAlert(bulkError);
  }
  const uploadHandler = () => {
    var data = [];
    for (var i = 0; i < excelData.length; i++) {
      data.push({
        first_name: excelData[i].Firstname,
        last_name: excelData[i].Lastname,
        email: excelData[i].Email,
        ...formData,
      });
    }
    if (data.length === 0) {
      return toast.error("No Data to upload");
    }
    bulk(data);
  };

  const singleHandler = () => {
    var data = [formData];

    bulk(data);
  };
  return (
    <Layout name="User" title="Users">
      <div className="report_container">
        <div className="button_container">
          <button
            className={user ? "btn btnPurple" : "btn"}
            onClick={() => {
              setUser(!user);
            }}
          >
            All Staffs
          </button>
          <button
            button
            className={!user ? "btn btnPurple" : "btn"}
            onClick={() => {
              setUser(!user);
            }}
          >
            Admins
          </button>
          {!user ? (
            <button
              button
              className={"btn btnGreenPry"}
              onClick={() => {
                setOpen(true);
              }}
            >
              Add Admin
            </button>
          ) : null}
        </div>
        <div className="table_container">
          {user ? (
            <>
              <div className="btnContainer">
                <button
                  type="button"
                  onClick={singleStaffHandler}
                  className="btn btnSuccess"
                >
                  Add Staff
                </button>
                <button
                  type="button"
                  onClick={openBulkHandler}
                  className="btn btnYellow"
                >
                  Staff Bulk Upload
                </button>
              </div>
              <Table
                columns={columns}
                data={data}
                actions={actions}
                selectID="id"
              />
            </>
          ) : (
            <Table
              columns={columns}
              data={admin}
              actions={adminActions}
              selectID="id"
            />
          )}
        </div>
        <Modal
          isVisible={open}
          title={""}
          size="md"
          content={
            <div className="modal_form_container">
              <FormGroup
                onSubmit={submitHandler}
                validation={formData}
                errors={errors}
                setErrors={setErrors}
                className="Form"
              >
                <Input
                  name="first_name"
                  label="First Name"
                  value={formData["first_name"]}
                  onChange={handleChange}
                  type="text"
                  validationHandler={validationHandler}
                  error={errors.first_name}
                  required={true}
                  readOnly={true}
                  size="large"
                  className="formInput"
                />

                <Input
                  name="last_name"
                  label="Last Name"
                  value={formData["last_name"]}
                  onChange={handleChange}
                  type="text"
                  validationHandler={validationHandler}
                  error={errors.last_name}
                  required={true}
                  readOnly={true}
                  size="large"
                  className="formInput"
                />
                <Input
                  name="email"
                  label="Email"
                  value={formData["email"]}
                  onChange={handleChange}
                  type="text"
                  validationHandler={validationHandler}
                  error={errors.email}
                  required={true}
                  readOnly={true}
                  size="large"
                  className="formInput"
                />

                <Select
                  name="location_id"
                  label="Location"
                  value={formData["location_id"]}
                  onChange={handleChange}
                  type="text"
                  validationHandler={validationHandler}
                  error={errors.location_id}
                  required={true}
                  data={locations}
                  filter={"name"}
                  filterValue={"id"}
                  size="large"
                  className="formInput"
                />

                <Select
                  name="is_on_leave"
                  label="Is on Leave"
                  value={formData["is_on_leave"]}
                  onChange={handleChange}
                  type="checkbox"
                  validationHandler={validationHandler}
                  error={errors.is_on_leave}
                  // required={true}
                  data={[
                    { name: "True", value: true },
                    { name: "False", value: false },
                  ]}
                  filter={"name"}
                  filterValue={"value"}
                  size="large"
                  className="formInput"
                />

                <Button
                  size="large"
                  type="submit"
                  title={isLoading ? "loading.." : "Add"}
                  disabled={isLoading ? true : false}
                  className="formButton btnPurple"
                />
              </FormGroup>
            </div>
          }
          onClose={() => setOpen(false)}
          footer=""
        />

        <Modal
          isVisible={openEdit}
          title={""}
          size="md"
          content={
            <div className="modal_form_container">
              <FormGroup
                onSubmit={editHandler}
                validation={formData}
                errors={errors}
                setErrors={setErrors}
                className="Form"
              >
                <Input
                  name="first_name"
                  label="First Name"
                  value={formData["first_name"]}
                  onChange={handleChange}
                  type="text"
                  validationHandler={validationHandler}
                  error={errors.first_name}
                  required={true}
                  readOnly={true}
                  size="large"
                  className="formInput"
                />

                <Input
                  name="last_name"
                  label="Last Name"
                  value={formData["last_name"]}
                  onChange={handleChange}
                  type="text"
                  validationHandler={validationHandler}
                  error={errors.last_name}
                  required={true}
                  readOnly={true}
                  size="large"
                  className="formInput"
                />
                <Input
                  name="email"
                  label="Email"
                  value={formData["email"]}
                  onChange={handleChange}
                  type="text"
                  validationHandler={validationHandler}
                  error={errors.email}
                  required={true}
                  readOnly={true}
                  size="large"
                  className="formInput"
                />

                <Select
                  name="location_id"
                  label="Location"
                  value={formData["location_id"]}
                  onChange={handleChange}
                  type="text"
                  validationHandler={validationHandler}
                  error={errors.location_id}
                  required={true}
                  data={locations}
                  filter={"name"}
                  filterValue={"id"}
                  size="large"
                  className="formInput"
                />

                <Select
                  name="is_on_leave"
                  label="Is on Leave"
                  value={formData["is_on_leave"]}
                  onChange={handleChange}
                  type="checkbox"
                  validationHandler={validationHandler}
                  error={errors.is_on_leave}
                  // required={true}
                  data={[
                    { name: "True", value: true },
                    { name: "False", value: false },
                  ]}
                  filter={"name"}
                  filterValue={"value"}
                  size="large"
                  className="formInput"
                />

                <Button
                  size="large"
                  onClick={editHandler}
                  type="button"
                  title={isLoading ? "loading.." : "Save"}
                  disabled={isLoading ? true : false}
                  className="formButton"
                />
              </FormGroup>
            </div>
          }
          onClose={closeHandler}
          footer=""
        />

        <Modal
          isVisible={singleStaff}
          title={"Create User Account"}
          size="md"
          content={
            <div className="modal_form_container">
              <FormGroup
                onSubmit={singleHandler}
                validation={formData}
                errors={errors}
                setErrors={setErrors}
                className="Form"
              >
                <Input
                  name="first_name"
                  label="First Name"
                  value={formData["first_name"]}
                  onChange={handleChange}
                  type="text"
                  validationHandler={validationHandler}
                  error={errors.first_name}
                  required={true}
                  readOnly={true}
                  size="large"
                  className="formInput"
                />

                <Input
                  name="last_name"
                  label="Last Name"
                  value={formData["last_name"]}
                  onChange={handleChange}
                  type="text"
                  validationHandler={validationHandler}
                  error={errors.last_name}
                  required={true}
                  readOnly={true}
                  size="large"
                  className="formInput"
                />
                <Input
                  name="email"
                  label="Email"
                  value={formData["email"]}
                  onChange={handleChange}
                  type="text"
                  validationHandler={validationHandler}
                  error={errors.email}
                  required={true}
                  readOnly={true}
                  size="large"
                  className="formInput"
                />

                <Select
                  name="location_id"
                  label="Location"
                  value={formData["location_id"]}
                  onChange={handleChange}
                  type="text"
                  validationHandler={validationHandler}
                  error={errors.location_id}
                  required={true}
                  data={locations}
                  filter={"name"}
                  filterValue={"id"}
                  size="large"
                  className="formInput"
                />

                <Button
                  size="large"
                  type="submit"
                  title={isLoading ? "loading.." : "Add"}
                  disabled={isLoading ? true : false}
                  bgColor="btnYellow"
                />
              </FormGroup>
            </div>
          }
          onClose={() => setSingleStaff(false)}
          footer=""
        />

        <Modal
          isVisible={openBulk}
          title={"Employee Bulk Upload"}
          size="md"
          content={
            <div className="bulkModal">
              <h4>Download Sample File</h4>
              <br />
              <div className="downloadTemp">
                <span>
                  <img src={excel} alt="Excel Icon" />
                  Sample items upload
                </span>
                <a href={excelTemp} download>
                  Download
                </a>
              </div>
              <h4>Upload .xlsx file</h4>
              <br />
              {upload ? (
                <ThreeDots width="200" color="#c21f4a" />
              ) : (
                <div className="bulkUploadModal">
                  <FaFileExcel /> <h5>Select an XLSX file to upload</h5>
                  <input type="file" onChange={fileChange} />
                </div>
              )}
              {excelData?.length > 0 && (
                <>
                  <Select
                    name="location_id"
                    label="Location"
                    value={formData["location_id"]}
                    onChange={handleChange}
                    type="text"
                    validationHandler={validationHandler}
                    error={errors.location_id}
                    required={true}
                    data={locations}
                    filter={"name"}
                    filterValue={"id"}
                    size="large"
                    className="formInput"
                  />
                </>
              )}
              <div className="modalBtnContainer right">
                <Button
                  title="Cancel"
                  loading={isLoading === 1}
                  disabled={isLoading === 1}
                  bgColor="btnBlack"
                  size="small"
                  onClick={openBulkHandler}
                  type="button"
                />

                <Button
                  title="Upload"
                  loading={isLoading === 1}
                  disabled={isLoading === 1}
                  bgColor="btnYellow"
                  size="small"
                  onClick={uploadHandler}
                  type="button"
                />
              </div>
            </div>
          }
          onClose={() => setOpenBulk(false)}
          footer=""
        />
      </div>
    </Layout>
  );
};

export default Users;
