import { useNavigate } from "react-router-dom";
import { axiosInstance } from "../../../../axios-Instance";
import { useMutation, useQueryClient } from "@tanstack/react-query";
import { errorAlert, successAlert } from "../../../../utils";
import { queryKeys } from "../../../../react-query/constants";

async function userRegister(formData) {
  // Convert JSON data to FormData
  const newFormData = new FormData();
  // formData["file"] = formData?.image // the backend needs "file", the formdata has "image"
  Object.keys(formData).forEach((key) => {
    newFormData.append(key, formData[key]);
  });
  const data = await axiosInstance({
    url: "/user",
    method: "POST",
    data: newFormData,
    headers: {
      "Content-Type": "multipart/form-data",
    },
  });

  return data?.data;
}

async function BulkUpload(excelData) {
  const result = await Promise.allSettled(
    excelData.map(async (formData) => {
      try {
        const response = await axiosInstance({
          url: `/user`,
          method: "POST",
          data: formData,
          headers: {
            "Content-Type": "multipart/form-data",
          },
        });
        return await response;
      } catch (err) {
        return err;
      }
    })
  );

  return await result;
}

export function useRegister() {
  const navigate = useNavigate();
  const { mutate, isError, error, isSuccess, reset, data } = useMutation({
    mutationFn: (formData) => userRegister(formData),
    onSuccess: () => {
      successAlert("Registration Successful");
      // setTimeout(() => {
      // 	navigate(PublicPaths.HOME);
      // }, 3000);
    },
    onError: (error) => {
      errorAlert(error);
    },
  });
  return { mutate, isError, error, isSuccess, reset, data };
}

export function useBulkRegister() {
  const queryClient = useQueryClient();
  const { mutate, isError, error, isSuccess, reset, data } = useMutation({
    mutationFn: (formData) => BulkUpload(formData),
    onSuccess: () => {
      queryClient.invalidateQueries([queryKeys.user]);
    },
  });
  return { mutate, isError, error, isSuccess, reset, data };
}
